module.exports = function (interpreter, window) {
  interpreter.setProperty(window,
    'App',
    interpreter.createNativeFunction((data) => {
      App({
        ...data.properties,
        onLaunch () {
          debugger;
        }
      });
    })
  );

  interpreter.setProperty(
    window,
    'Page',
    interpreter.createNativeFunction((data) => {
      Page(data);
    })
  )

  interpreter.setProperty(
    window,
    'console',
    interpreter.nativeToPseudo(console)
  )

  interpreter.setProperty(
    window,
    'wx',
    interpreter.nativeToPseudo(wx)
  );
}